<template>
  <div class="news-detail-wrap">
    <div class="content-wrap">
      <div class="title">{{ info.title }}</div>
      <div class="info">
        <div class="item">
          <img :src="info.logo || defaultImg" alt="" translate="no" />
          <div class="name" translate="no">{{ info.author || '-' }}</div>
        </div>
        <div class="item">
          <span>{{ info.readingVolume }}</span
          >{{ $t('admin.read') }}
        </div>
        <div class="item">{{ info.updateTime || info.createTime  }}</div>
      </div>
      <div class="content" v-html="info.content"></div>
    </div>
  </div>
</template>

<script>
import { handleNewsDetail } from '@/services/news'

export default {
  name: '',
  data () {
    return {
      info: {
        title: '',
        content: null,
        type: 1,
        preview: '',
        isImportant: 0
      },
      defaultImg: require('../../assets/logo.png')
    }
  },
  methods: {
    // 获取详情
    async getDetail (id) {
      const resp = await handleNewsDetail(id)
      if (!resp.success) return
      this.info = resp.data
    }
  },
  mounted () {
    if (this.$route.query.id) {
      this.getDetail(this.$route.query.id)
    }
  }
}
</script>

